import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Portal from "../views/Portal.vue";
import Auto from "../views/Auto.vue";
import Sec from "../views/Sec.vue";
import About from "../views/About.vue";
import Contact from "../views/Contact.vue";
import Process from "../views/Process.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/portal",
    name: "Portal systems",
    component: Portal,
  },
  {
    path: "/auto",
    name: "Automated integrations",
    component: Auto,
  },
  {
    path: "/security",
    name: "Security & Surveillance",
    component: Sec,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
  },
  {
    path: "/process",
    name: "Process",
    component: Process,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
